<template>
  <el-row :gutter="10">
    <el-col :span="4">
      <div class="list_box">
        <div class="title">测试场景</div>
        <el-button
            @click="addScene"
            icon="Plus"
            plain
            type="success"
            style="width: 100%;"
            :loading="addingScene"
        >
          添加测试场景
        </el-button>
        <el-menu
            :default-active="active"
            class="el-menu-vertical-demo"
            active-text-color="#00aa7f"
            text-color="#000000"
            style="background: none;border: none;"
        >
          <el-menu-item
              v-for="item in testScenes"
              :key="item.id"
              :index="item.id.toString()"
              @click="handleSceneClick(item)"
              style="height: 35px;line-height: 35px;"
              :disabled="loading"
          >
            <el-icon>
              <HelpFilled/>
            </el-icon>
            <span>{{ item.name }}</span>
          </el-menu-item>
        </el-menu>
      </div>
    </el-col>

    <el-col :span="20">
      <el-scrollbar height="calc(100vh - 68px)">
        <div v-loading="loading" class="scene-data-box" v-if="steps !== null">
          <el-divider content-position="left"><b>场景信息</b></el-divider>
          <el-row :gutter="5" style="margin-bottom: 20px;">
            <el-col :span="12">
              <el-input v-model="sceneInfo.name" size="small">
                <template #prepend>场景名称</template>
                <template #append>
                  <el-button
                      @click="saveScene"
                      plain
                      icon="CircleCheck"
                      type="success"
                      :loading="saving"
                  >
                    保存
                  </el-button>
                </template>
              </el-input>
            </el-col>
            <el-col :span="12" style="text-align: right;">
              <el-button
                  @click="runScene"
                  plain
                  type="success"
                  icon="Promotion"
                  :disabled="!envId"
                  :loading="running"
              >
                运行
              </el-button>
              <el-button
                  @click="deleteScene"
                  plain
                  icon="Delete"
                  type="danger"
                  :loading="deleting"
              >
                删除
              </el-button>
            </el-col>
          </el-row>

          <el-divider content-position="left"><b>执行步骤</b></el-divider>
          <el-scrollbar height="calc(100vh - 210px)">
            <el-table
                :data="steps"
                row-key="id"
                style="width: 100%;margin-bottom: 10px;"
                :show-header="false"
                v-loading="loadingSteps"
            >
              <el-table-column>
                <template #default="scope">
                  <span class="el-icon-upload" style="color: #009A71;font-weight: bold;font-size: 14px;">
                    {{ '步骤' + (scope.$index + 1) + ': ' }}
                  </span>
                  <span style="font-weight: bold;font-size: 14px;">{{ scope.row.stepInfo.title }}</span>
                </template>
              </el-table-column>
              <el-table-column width="200px">
                <template #default="scope">
                  <el-button
                      @click="clickEditStep(scope.row.stepInfo)"
                      plain
                      type="success"
                      icon="Edit"
                  >
                    编辑
                  </el-button>
                  <el-button
                      @click="clickDelete(scope.row.id)"
                      plain
                      type="danger"
                      icon="Delete"
                  >
                    删除
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
            <el-button @click="addStepDlg = true" plain icon="Plus" type="success">新增步骤</el-button>
          </el-scrollbar>
        </div>

        <el-empty v-else :description="testScenes.length === 0 ? '请添加一个测试场景' : '请选择一个测试场景'"/>
      </el-scrollbar>
    </el-col>
  </el-row>

  <!-- 运行结果抽屉 -->
  <el-drawer
      v-model="ResultDlg"
      :with-header="false"
      size="60%"
      :before-close="handleDrawerClose"
  >
    <div style="padding:20px;">
      <el-descriptions title="执行结果" border :column="4">
        <el-descriptions-item label="总数">{{ runSceneResult.all }}</el-descriptions-item>
        <el-descriptions-item label="通过">{{ runSceneResult.success }}</el-descriptions-item>
        <el-descriptions-item label="失败">{{ runSceneResult.fail }}</el-descriptions-item>
        <el-descriptions-item label="错误">{{ runSceneResult.error }}</el-descriptions-item>
      </el-descriptions>
      <div style="height: 40px;line-height: 40px;"><b>执行详情</b></div>
      <el-scrollbar height="calc(100vh - 180px)">
        <el-table :data="runSceneResult.cases" style="width: 100%">
          <el-table-column type="expand">
            <template #default="props">
              <caseResult :result="props.row"></caseResult>
            </template>
          </el-table-column>
          <el-table-column label="用例名" prop="name"/>
          <el-table-column label="请求方法" prop="method"/>
          <el-table-column label="响应状态码" prop="status_cede"/>
          <el-table-column label="执行结果" prop="state" min-width="40px">
            <template #default="props">
              <el-tag :type="props.row.state === '成功' ? 'success' : 'danger'">
                {{ props.row.state }}
              </el-tag>
            </template>
          </el-table-column>
        </el-table>
      </el-scrollbar>
    </div>
  </el-drawer>

  <!-- 编辑用例抽屉 -->
  <el-drawer
      v-model="editStepDlg"
      :with-header="false"
      size="65%"
      :before-close="handleEditDrawerClose"
  >
    <EditCase
        :editCase="editCase"
        style="padding: 0 10px; min-height: 500px;"
        @saved="handleCaseSaved"
        @cancel="editStepDlg = false"
    />
  </el-drawer>

  <!-- 添加测试步骤抽屉 -->
  <el-drawer
      v-model="addStepDlg"
      :with-header="false"
      :before-close="handleAddStepClose"
  >
    <template #default>
      <el-tabs stretch v-model="activeTab">
        <el-tab-pane label="内部接口" name="internal">
          <el-tree
              ref="treeM1"
              :data="inters1"
              show-checkbox
              :props="addTreeProps"
              node-key="id"
              default-expand-all
              highlight-current
              :filter-node-method="filterNode"
          >
            <template #default="{ node, data }">
              <span class="custom-tree-node">
                <div v-if="data.name">
                  <b style="color:#00AA7F;">
                    <el-icon><Paperclip/></el-icon>
                    <span>{{ data.name }}</span>
                  </b>
                </div>
                <div v-if="data.title">
                  <b style="color:#00AA7F;">
                    <el-icon><Link/></el-icon>
                    <span>用例</span>
                  </b>
                  <span>: {{ data.title }}</span>
                </div>
              </span>
            </template>
          </el-tree>
        </el-tab-pane>
        <el-tab-pane label="外部接口" name="external">
          <el-tree
              ref="treeM2"
              :data="inters2"
              show-checkbox
              :props="addTreeProps"
              node-key="id"
              default-expand-all
              :expand-on-click-node="false"
              :filter-node-method="filterNode"
          >
            <template #default="{ node, data }">
              <span class="custom-tree-node">
                <div v-if="data.name">
                  <b style="color:#00AA7F;">
                    <el-icon><Paperclip/></el-icon>
                    <span>{{ data.name }}</span>
                  </b>
                </div>
                <div v-if="data.title">
                  <b style="color:#00AA7F;">
                    <el-icon><Link/></el-icon>
                    <span>用例</span>
                  </b>
                  <span>: {{ data.title }}</span>
                </div>
              </span>
            </template>
          </el-tree>
        </el-tab-pane>
      </el-tabs>

      <el-affix position="bottom" :offset="40">
        <div class="add-btns" style="text-align: center;">
          <el-tooltip content="将选择的用例加入到测试场景中" placement="top-start">
            <el-button
                plain
                type="success"
                size="small"
                @click="confirmAddToScene"
                :loading="addingSteps"
            >
              确认添加
            </el-button>
          </el-tooltip>
          <el-tooltip content="关闭当前窗口" placement="top-start">
            <el-button plain type="danger" size="small" @click="addStepDlg = false">
              关闭窗口
            </el-button>
          </el-tooltip>
        </div>
      </el-affix>
    </template>
  </el-drawer>
</template>

<script>
import {mapState, mapActions, mapGetters} from 'vuex';
import {ElNotification, ElMessage, ElMessageBox} from 'element-plus';
import Sortable from 'sortablejs';
import EditCase from '../components/CaseEdit.vue';
import caseResult from '../components/caseResult.vue';

export default {
  components: {
    EditCase,
    caseResult
  },
  data() {
    return {
      active: '1',
      sceneInfo: null,
      steps: null,
      addStepDlg: false,
      activeTab: 'internal',
      addTreeProps: {
        children: 'steps',
        label: 'name'
      },
      editStepDlg: false,
      editCase: null,
      runSceneResult: null,
      ResultDlg: false,
      loading: false,
      loadingSteps: false,
      addingScene: false,
      saving: false,
      running: false,
      deleting: false,
      addingSteps: false,
      sortableInstance: null,
      searchText: ''
    };
  },
  computed: {
    ...mapState(['pro', 'testScenes', 'envId']),
    ...mapGetters(['inters1', 'inters2']),
    hasSelectedEnv() {
      return !!this.envId;
    }
  },
  methods: {
    ...mapActions(['getAllScene', 'getAllInterFaces']),

    // 添加测试场景
    async addScene() {
      try {
        this.addingScene = true;
        const params = {
          name: 'New Scene',
          project: this.pro.id
        };
        const response = await this.$api.createTestScene(params);
        if (response.status === 201) {
          ElMessage.success('添加成功');
          await this.getAllScene();
        }
      } catch (error) {
        console.error('添加场景失败:', error);
        ElMessage.error('添加场景失败');
      } finally {
        this.addingScene = false;
      }
    },

    // 处理场景点击
    async handleSceneClick(scene) {
      if (this.loading) return;

      try {
        this.loading = true;
        await this.getSceneData(scene);
        this.active = scene.id.toString();
      } catch (error) {
        console.error('获取场景数据失败:', error);
        ElMessage.error('获取场景数据失败');
      } finally {
        this.loading = false;
      }
    },

    // 获取场景数据
    async getSceneData(scene) {
      try {
        this.loadingSteps = true;
        this.sceneInfo = {...scene};
        const response = await this.$api.getSceneDatas(scene.id);
        if (response.status === 200) {
          this.steps = response.data || null; // 若为空数组则设为 null
          this.$nextTick(this.initSortable);
        } else {
          throw new Error(`请求失败，状态码: ${response.status}`);
        }
      } catch (error) {
        console.error('获取场景步骤失败:', error);
        this.steps = null;
        ElMessage.error('获取场景步骤失败');
      } finally {
        this.loadingSteps = false;
      }
    }
    ,

    // 初始化拖拽排序
    initSortable() {
      try {
        const tbody = document.querySelector('.el-table__body-wrapper tbody');
        if (!tbody) {
          throw new Error('表格元素未找到');
        }

        if (this.sortableInstance) {
          this.sortableInstance.destroy();
        }

        this.sortableInstance = new Sortable(tbody, {
          animation: 150,
          ghostClass: 'sortable-ghost',
          onEnd: async (evt) => {
            const currRow = this.steps.splice(evt.oldIndex, 1)[0];
            this.steps.splice(evt.newIndex, 0, currRow);
            await this.updateSceneOrder();
          }
        });
      } catch (error) {
        console.error('初始化拖拽失败:', error);
        setTimeout(this.initSortable, 300);
      }
    },

    // 更新步骤顺序
    async updateSceneOrder() {
      try {
        this.steps.forEach((item, index) => {
          item.sort = index + 1;
        });

        const response = await this.$api.updateSceneDataOrder(this.steps);
        if (response.status !== 200) {
          throw new Error('更新顺序失败');
        }
        ElMessage.success('调整排序成功');
      } catch (error) {
        console.error('更新步骤顺序失败:', error);
        ElMessage.error('调整排序失败');
        await this.getSceneData(this.sceneInfo); // 恢复原始顺序
      }
    },

    // 保存场景
    async saveScene() {
      try {
        this.saving = true;
        const response = await this.$api.updateTestScene(this.sceneInfo.id, this.sceneInfo);
        if (response.status === 200) {
          ElMessage.success('保存成功');
          await this.getAllScene();
        } else {
          throw new Error('保存失败');
        }
      } catch (error) {
        console.error('保存场景失败:', error);
        ElMessage.error('保存失败');
      } finally {
        this.saving = false;
      }
    },

    // 删除场景
    async deleteScene() {
      try {
        await ElMessageBox.confirm('确定要删除该测试场景吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        });

        this.deleting = true;
        const response = await this.$api.delTestScene(this.sceneInfo.id);
        if (response.status === 204) {
          ElMessage.success('删除成功');
          await this.getAllScene();
          if (this.testScenes.length > 0) {
            await this.handleSceneClick(this.testScenes[0]);
          } else {
            this.sceneInfo = null;
            this.steps = null;
          }
        }
      } catch (error) {
        if (error !== 'cancel') {
          console.error('删除场景失败:', error);
          ElMessage.error('删除失败');
        }
      } finally {
        this.deleting = false;
      }
    },

    // 运行场景
    async runScene() {
      try {
        this.running = true;
        const params = {
          env: this.envId,
          scene: this.sceneInfo.id
        };

        ElMessage.success('开始运行!');
        const response = await this.$api.runTest(params);

        if (response.status === 200) {
          this.runSceneResult = response.data;
          this.ResultDlg = true;
        } else {
          throw new Error('运行失败');
        }
      } catch (error) {
        console.error('运行场景失败:', error);
        ElMessage.error('运行场景失败');
      } finally {
        this.running = false;
      }
    },

    // 点击编辑步骤
    clickEditStep(editCase) {
      this.editCase = editCase;
      this.editStepDlg = true;
    },

    // 处理案例保存
    handleCaseSaved() {
      this.editStepDlg = false;
      this.getSceneData(this.sceneInfo);
    },

    // 删除步骤
    async clickDelete(id) {
      try {
        await ElMessageBox.confirm('确定要删除该步骤吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        });

        const response = await this.$api.delSceneData(id);
        if (response.status === 204) {
          ElMessage.success('删除成功');
          await this.getSceneData(this.sceneInfo);
        }
      } catch (error) {
        if (error !== 'cancel') {
          console.error('删除步骤失败:', error);
          ElMessage.error('删除失败');
        }
      }
    },

    // 确认添加步骤
    async confirmAddToScene() {
      try {
        this.addingSteps = true;
        const checkedCases = this.getCheckedCases();
        if (checkedCases.length === 0) {
          ElMessage.warning('请先选择要添加的用例');
          return;
        }

        await this.addToScene(checkedCases);
        ElNotification.success({
          title: '成功',
          message: '用例添加成功',
          duration: 2000
        });
        this.addStepDlg = false;
      } catch (error) {
        console.error('添加步骤失败:', error);
        ElNotification.error({
          title: '失败',
          message: '添加用例失败',
          duration: 2000
        });
      } finally {
        this.addingSteps = false;
      }
    },

    // 获取选中的案例
    getCheckedCases() {
      const nodes1 = this.$refs.treeM1?.getCheckedNodes() || [];
      const nodes2 = this.$refs.treeM2?.getCheckedNodes() || [];
      return [...nodes1, ...nodes2].filter(node => node.title);
    },

    // 添加步骤到场景
    async addToScene(cases) {
      const currentOrder = this.steps.length;
      const requests = cases.map((caseItem, index) => {
        return this.$api.addSceneData({
          step: caseItem.id,
          scene: this.sceneInfo.id,
          sort: currentOrder + index + 1
        });
      });

      await Promise.all(requests);
      await this.getSceneData(this.sceneInfo);
      await this.getAllInterFaces();
    },

    // 过滤树节点
    filterNode(value, data) {
      if (!value) return true;
      return data.name.includes(value) || (data.title && data.title.includes(value));
    },

    // 处理抽屉关闭
    handleDrawerClose(done) {
      done();
    },

    // 处理编辑抽屉关闭
    handleEditDrawerClose(done) {
      if (this.editCase?.hasChanges) {
        ElMessageBox.confirm('还有未保存的修改，确定要关闭吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          done();
        }).catch(() => {
          // 不关闭
        });
      } else {
        done();
      }
    },

    // 处理添加步骤关闭
    handleAddStepClose(done) {
      const hasChecked = this.getCheckedCases().length > 0;
      if (hasChecked) {
        ElMessageBox.confirm('已选择用例但未添加，确定要关闭吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          done();
        }).catch(() => {
          // 不关闭
        });
      } else {
        done();
      }
    }
  },
  async created() {
    try {
      this.loading = true;
      await this.getAllScene();

      if (this.testScenes.length > 0) {
        this.loading = false;
        await this.handleSceneClick(this.testScenes[0]);
        this.loading = true;
      }
    } catch (error) {
      console.error('初始化失败:', error);
      ElMessage.error('初始化测试场景失败');
    } finally {
      this.loading = false;
    }
  },
  beforeUnmount() {
    if (this.sortableInstance) {
      this.sortableInstance.destroy();
    }
  }
};
</script>

<style scoped>
.list_box {
  height: calc(100vh - 68px);
  box-shadow: 0 0 5px #00aa7f;
  background: #fff;
  border-radius: 4px;
  padding: 10px;
}

.list_box .title {
  text-align: center;
  font: bold 20px/40px 'microsoft yahei';
  border-bottom: solid 2px #e6e6e6;
  margin-bottom: 10px;
  color: #333;
}

.scene-data-box {
  padding: 20px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);
}

.custom-tree-node {
  width: 100%;
}

.sortable-ghost {
  opacity: 0.5;
  background: #c8ebfb;
}

.env-value {
  word-break: break-all;
}
</style>

<style>
.el-table__body-wrapper tbody tr.sortable-ghost td {
  background: #f0f9eb;
}
</style>
